<template>
  <div>
    <div style="margin: 0 auto;"><el-transfer  v-model="value" :data="data"></el-transfer></div>
    <div style="margin: 0 auto;"><el-button type="primary">主要按钮</el-button></div>
    <div  style="margin: 0 auto;"><el-tag>标签一</el-tag></div>
  </div>
</template>

<script>
export default {
  data() {
    const generateData = (_) => {
      const data = [];
      for (let i = 1; i <= 15; i++) {
        data.push({
          key: i,
          label: `备选项 ${i}`,
          disabled: i % 4 === 0,
        });
      }
      return data;
    };
    return {
      data: generateData(),
      value: [1, 4],
    };
  },
};
</script>

<style>

</style>


